<template>
	<div>
    <!-- 載入畫面元件 -->
    <loading :active.sync="isLoading"></loading>
    <Alert></Alert>
    
    <Navbar></Navbar>
    <div class="container-fluid">
      <div class="row">
        <Sidebar></Sidebar>
        <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-4">
          <router-view v-on:load='loading'></router-view>
        </main>
      </div>
    </div>
	</div>
</template>

<script>
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import Alert from "./AlertMessage";

export default {
  name: "Dashboard",
  components: {
    Navbar,
    Sidebar,
    Alert,
  },
  data() {
    return{
      isLoading: false,
    }
  },
  methods: {
    loading(isloading) {
      this.isLoading = isloading;
    }
  }
}
</script>


